<template>
  <v-container>
    <h3 style="color: #312E7D;margin-bottom:10px;margin-top:10px;">Provider Directory</h3>
    <v-layout row wrap>
      <v-flex>
        <v-card>
          <v-tabs v-model="tab" centered fixed-tabs icons-and-text background-color="#4d9bcb" color="#312E7D" slider-color="#312E7D" dark @change="clearFields">
            <v-tab href="#tab-1">
              Specialty
              <i class="fas fa-stethoscope"></i>
            </v-tab>
            <v-tab href="#tab-3">
              Name
              <i class="fas fa-user-md"></i>
            </v-tab>
            <v-tab href="#tab-4">
              Practice
              <i class="fas fa-clinic-medical"></i>
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item value="tab-1">
              <v-card flat>
                <v-card-text>
                  <v-autocomplete
                    ref="objSpecialty"
                    v-model="searchItem"
                    :items="searchSpecialties"
                    :loading="isSpecialtyLoading"
                    :search-input.sync="searchSpecialtyString"
                    item-text="SearchResult"
                    item-value="SearchResult"
                    dense
                    outlined
                    label="Specialty Lookup"
                    placeholder="Start typing to Search (minimum 2 characters)"
                  ></v-autocomplete>
                </v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item value="tab-3">
              <v-card flat>
                <v-card-text>
                  <v-autocomplete
                    ref="objName"
                    v-model="searchItem"
                    :items="searchProvider"
                    :loading="isProviderLoading"
                    :search-input.sync="searchProviderString"
                    item-text="SearchResult"
                    item-value="SearchKey"
                    outlined
                    label="Provider Lookup"
                    placeholder="Start typing to Search (minimum 2 characters)"
                  ></v-autocomplete>
                </v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item value="tab-4">
              <v-card flat>
                <v-card-text>
                  <v-autocomplete
                    ref="objPractice"
                    v-model="searchItem"
                    :items="searchPractice"
                    :loading="isPracticeLoading"
                    :search-input.sync="searchPracticeString"
                    item-text="SearchResult"
                    item-value="SearchKey"
                    outlined
                    label="Practice Lookup"
                    placeholder="Start typing to Search (minimum 2 characters)"
                  ></v-autocomplete>
                </v-card-text>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
          <v-row>
            <div style="margin-left: 30px;font-size: 12px;">Optional</div>
          </v-row>
          <v-row cols="12">
            <v-divider style="margin-left:30px;margin-right:30px;margin-bottom:5px;"></v-divider>
          </v-row>
          <v-row>
            <v-col cols="2" style="margin-left:16px;padding-top:0px;">
              <v-combobox hide-details dense v-model="searchLocation" items_notused="locationList" placeholder="Zip Code" outlined clearable append-icon>
                <template slot="item" slot-scope="data">
                  <v-icon>near_me</v-icon>
                  {{ data.item }}
                </template>
              </v-combobox>
            </v-col>
            <v-col cols="2" style="padding-top:0px;">
              <v-combobox
                ref="locRange"
                dense
                hide-details
                v-model="searchLocationRange"
                :item-value="locationRangeList.value"
                :item-text="locationRangeList.text"
                :items="locationRangeList"
                placeholder="Distance"
                outlined
                :error="missingRange"
              ></v-combobox>
            </v-col>
            <v-col cols="2" offset="1" style="padding-top:0px;">
              <v-combobox hide-details dense v-model="searchGender" :item-text="genderList.text" :items="genderList" placeholder="Gender" outlined></v-combobox>
            </v-col>
            <v-col cols="3" style="padding-top:0px;">
              <v-checkbox v-model="searchStatVisits" value="Y" label="Stat Visits Accepted" dense hide-details></v-checkbox>
            </v-col>
          </v-row>

          <v-btn ref="btnSearch" @click="search" class="chs-button chs-color">Search</v-btn>
        </v-card>
      </v-flex>
    </v-layout>
    <div v-if="displayResults">
      <v-layout row wrap>
        <div style="color:rgba(36,32,44,.5); margin-bottom: 15px;">Search Results: {{ selectedItems.length }} record(s) found.</div>
      </v-layout>
      <!-- <v-layout row wrap v-for="provider in selectedItems" v-bind:key="provider.npi" class="mb-5 pa-3 provider-row" :class="provider.Tier"> -->
      <v-layout row wrap v-for="provider in selectedItems" v-bind:key="provider.npi" class="mb-5 pa-3 provider-row" v-show="showTier(provider.Tier)">
        <v-col cols="12" sm="12" md="6" class="content-container">
          <div class="mb-3">
            <v-container full style="padding-top:1px;">
              <v-row>
                <v-col cols="9" style="padding:0px;">
                  <h3 @click="showProvider(provider)" class="link">
                    {{ provider.Prov_Name + ", " + provider.Degree }}
                  </h3>
                </v-col>
                <v-col cols="3" style="padding:0px;">
                  <v-btn small color="#49157c" @click="showProvider(provider)" class="link" dark style="float:right;width:100%;">VIEW FULL BIO</v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="9" style="padding:0px;">
                  <div v-for="specialty in provider.Speciality.split(';')" :key="specialty">
                    <div>{{ specialty }}</div>
                  </div>
                </v-col>
                <v-col cols="3" style="padding:0px;">
                  <div style="text-align:center;">{{ provider.Tier }}</div>
                </v-col></v-row
              ></v-container
            >
          </div>
          <h4>Hospital Affiliations</h4>
          <div v-if="provider.Hospital_Affiliations == null" style="margin-bottom:50px;"></div>
          <div v-for="(aff, index) in provider.Hospital_Affiliations" v-bind:key="index">
            <div v-if="index === Object.keys(provider.Hospital_Affiliations).length - 1" style="margin-bottom:50px;">{{ aff.Hospital_Name }}</div>
            <div v-else>{{ aff.Hospital_Name }}</div>
          </div>
          <div class="content-bottom" v-if="provider.Provider_Type.toLowerCase() === 'captive'" align-center>
            <v-img alt="CHSLI Logo" class="shrink mr-2" contain src="https://www.chsli.org/themes/custom/chsli/logo.png" transition="scale-transition" width="100" style="float:left;" />
            <span class="employed">CHS Physician Partners Medical Group</span>
          </div>
        </v-col>
        <v-col cols="12" sm="6" md="2">
          <v-img class="results" contain height="auto" width="150" v-bind:src="`${imagePath}/${provider.NPI}.jpg`"></v-img>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <div v-for="(address, index) in provider.Location" v-bind:key="index">
            <div v-if="inRange(address.Distance)">
              <v-card v-if="index === 0 || showLocation(provider.NPI)" :key="refreshKey" style="margin-bottom: 5px;">
                <v-card-text>
                  <div>{{ address.Practice }}</div>
                  <div>{{ address.Address }}</div>
                  <div>{{ address.City + ", " + address.State + " " + address.Zip }}</div>
                  <div v-if="address.Phone">
                    <span class="material-icons" style="vertical-align: middle;">call</span>
                    {{ address.Phone }}
                  </div>
                  <div v-if="address.Fax">
                    <span class="material-icons" style="vertical-align: middle;">print</span>
                    {{ address.Fax }}
                  </div>
                  <div v-if="address.Distance && searchLocation != ''" class="link" style="margin-top: 5px;" @click="showMap(`${address.Address} ${address.Zip}`)">
                    <u>
                      <span class="material-icons" style="vertical-align: middle;">location_on</span>
                      {{ parseFloat(address.Distance).toFixed(1) }} miles away
                    </u>
                  </div>
                </v-card-text>
              </v-card>
              <div v-if="provider.Location.length > 1 && index === provider.Location.length - 1">
                <v-btn v-if="provider.NPI != locationNpiToShow" text small @click="toggleLocation(provider, provider.Location.length)">
                  Show All Locations
                  <span class="material-icons">keyboard_arrow_down</span>
                </v-btn>
                <v-btn v-if="provider.NPI === locationNpiToShow" text small @click="toggleLocation('')">
                  Hide All Locations
                  <span class="material-icons">keyboard_arrow_up</span>
                </v-btn>
              </div>
            </div>
          </div>
        </v-col>
      </v-layout>
    </div>
    <!--------------Print View-------------------->
    <div id="printContainer" class="do-not-display">
      <v-container>
        <v-layout row wrap>
          <h1>Catholic Health Referral Listing</h1>
        </v-layout>
        <v-layout row wrap>
          <v-col>
            <div>{{ searchItem.value }}</div>
          </v-col>
          <v-col>
            <div v-if="searchLocation">Location: {{ searchLocation }} within {{ searchLocationRange.text }}</div>
          </v-col>
          <v-col></v-col>
        </v-layout>
        <v-layout row wrap>
          <div style="color:rgba(36,32,44,.5); margin-bottom: 15px;">Search Results: {{ selectedItems.length }} record(s) found.</div>
        </v-layout>
        <v-layout row wrap>
          <v-col><v-divider></v-divider></v-col>
        </v-layout>
        <v-layout row wrap v-for="provider in selectedItems" v-bind:key="provider.npi" class="mb-5 pa-3 provider-row" v-show="showTier(provider.Tier)">
          <v-col cols="12" sm="12" md="6" class="content-container">
            <h3>
              {{ provider.Prov_Name + ", " + provider.Degree }}
            </h3>
            <div class="mb-3">
              <div v-for="specialty in provider.Speciality.split(';')" :key="specialty">
                <div>{{ specialty }}</div>
              </div>
            </div>
            <h4>Hospital Affiliations</h4>
            <div v-if="provider.Hospital_Affiliations == null" style="margin-bottom:50px;"></div>
            <div v-for="(aff, index) in provider.Hospital_Affiliations" v-bind:key="index">
              <div v-if="index === Object.keys(provider.Hospital_Affiliations).length - 1" style="margin-bottom:50px;">{{ aff.Hospital_Name }}</div>
              <div v-else>{{ aff.Hospital_Name }}</div>
            </div>
            <div class="content-bottom" v-if="provider.Provider_Type.toLowerCase() === 'captive'" align-center>
              <span class="employed">CHS Physician Partners Medical Group</span>
            </div>
          </v-col>
          <v-col cols="12" sm="6" md="2">
            <div>{{ provider.Tier }}</div>
          </v-col>
          <v-col cols="12" sm="6" md="4">
            <div v-for="(address, index) in provider.Location" v-bind:key="index">
              <div v-if="inRange(address.Distance)">
                <v-card style="margin-bottom: 5px;">
                  <v-card-text>
                    <div v-if="index != 0">
                      <v-divider></v-divider>
                    </div>
                    <div>{{ address.Practice }}</div>
                    <div>{{ address.Address }}</div>
                    <div>{{ address.City + ", " + address.State + " " + address.Zip }}</div>
                    <div v-if="address.Phone">Phone: {{ address.Phone }}</div>
                    <div v-if="address.Fax">Fax: {{ address.Fax }}</div>
                    <div v-if="address.Distance && searchLocation != ''" class="link">
                      <u> {{ parseFloat(address.Distance).toFixed(1) }} miles away </u>
                    </div>
                  </v-card-text>
                </v-card>
              </div>
            </div>
          </v-col>
          <v-col cols="12">
            <v-divider></v-divider>
          </v-col> </v-layout
      ></v-container>
    </div>
    <!--------------Print View-------------------->
    <v-btn ref="tier1btn" v-scroll="onScroll" v-show="showTierButtons" fab dark fixed bottom right :color="toggleColor(1)" @click="toggleTier(1)" style="right:288px;">Tier1<br>({{tier1Count}})</v-btn>
    <v-btn ref="tier2btn" v-scroll="onScroll" v-show="showTierButtons" fab dark fixed bottom right :color="toggleColor(2)" @click="toggleTier(2)" style="right:224px;">Tier2<br>({{tier2Count}})</v-btn>
    <v-btn ref="tier3btn" v-scroll="onScroll" v-show="showTierButtons" fab dark fixed bottom right :color="toggleColor(3)" @click="toggleTier(3)" style="right:160px;">Tier3<br>({{tier3Count}})</v-btn>
    <v-btn v-scroll="onScroll" v-show="displayResults" fab dark fixed bottom right color="primary" @click="toPrint" style="right:80px;">
      <v-icon>print</v-icon>
    </v-btn>
    <v-btn v-scroll="onScroll" v-show="fab" fab dark fixed bottom right color="primary" @click="toTop">
      <v-icon>keyboard_arrow_up</v-icon>
    </v-btn>
    <v-snackbar v-model="displayAlert" color="error" top
      >{{ alertMessage }}
      <v-btn color="black" text right @click="displayAlert = false"> Close </v-btn>
    </v-snackbar>
    <!-- GmapMap needed so i can use gecode in code -->
    <GmapMap ref="gmap" :center="{ lat: 10, lng: 10 }" :zoom="15" map-type-id="roadmap" style="width: 0px; height: 0px;"></GmapMap>
  </v-container>
</template>

<script>
import router from "../router";
const axios = require("axios");
import { gmapApi } from "vue2-google-maps";
import fullConfig from "../config";

const Config = fullConfig[process.env.NODE_ENV];

export default {
  name: "CHSoogle",
  data: () => {
    return {
      refreshKey: 0,
      fab: false,
      tab: null,
      selectedItems: [
        {
          lastname: null,
          firstname: null,
          title: null,
          Speciality: "",
          Provider_Type: "",
        },
      ],
      isSpecialtyLoading: false,
      searchSpecialtyString: "",
      searchSpecialties: [],
      isProviderLoading: false,
      searchProviderString: "",
      searchProvider: [],
      isPracticeLoading: false,
      searchPracticeString: "",
      searchPractice: [],
      searchItem: "",
      searchLocation: null,
      searchLocationRange: null,
      searchGender: { abbr: "No Preference", text: "No Preference" },
      searchStatVisits: null,
      displayResults: false,
      hidden: true,
      locationNpiToShow: "",
      locationList: ["Current Location"],
      locationRangeList: [
        { value: 1, text: "1 Mile" },
        { value: 2, text: "2 Miles" },
        { value: 3, text: "3 Miles" },
        { value: 4, text: "4 Miles" },
        { value: 5, text: "5 Miles" },
        { value: 15, text: "15 Miles" },
        { value: 25, text: "25 Miles" },
      ],
      genderList: [
        { abbr: "F", text: "Female" },
        { abbr: "M", text: "Male" },
        { abbr: "No Preference", text: "No Preference" },
      ],
      imagePath: Config.imageUrl,
      error: false,
      displayAlert: false,
      alertMessage: "",
      tier1Visible: true,
      tier2Visible: false,
      tier3Visible: false,
      isSearching: false,
      missingRange: false,
    };
  },
  computed: {
    google: gmapApi,
    showTierButtons: function() {
      return (
        this.displayResults &&
        document
          .getElementsByClassName("v-tab--active")[0]
          .innerText.trim()
          .toUpperCase() === "SPECIALTY"
      );
    },
    tier1Count: function() {
      return this.selectedItems.filter(el => el.Tier === 'Tier1').length;   
    },
    tier2Count: function() {
      return this.selectedItems.filter(el => el.Tier === 'Tier2').length;   
    },
    tier3Count: function() {
      return this.selectedItems.filter(el => el.Tier === 'Tier3').length;   
    }
  },
  watch: {
    searchItem() {
      // on name and practice tab only
      if (
        document.getElementsByClassName("v-tab--active")[0] &&
        document
          .getElementsByClassName("v-tab--active")[0]
          .innerText.trim()
          .toUpperCase() !== "SPECIALTY"
      ) {
        this.search();
      }
    },
    searchLocation(val) {
      if (val == null || val === "") {
        // reset range
        this.searchLocationRange = "";
      }
    },
    searchSpecialtyString(val) {
      if (val == null || val.length === 0) {
        this.searchSpecialties = [];
      } // clear for a new search

      if (val == null || val.length < 2) {
        return;
      } // let them type 3 before we search

      if (this.isSpecialtyLoading) {
        return;
      } // Items have already been requested

      this.isSpecialtyLoading = true;
      this.selectedItems = [];  // clear results

      axios(`${Config.apiRoot}/lookup?type=Specialty&value=${val}`)
        .then((res) => {
          if (res.data.success) {
            this.searchSpecialties = res.data.message;
          }
        })
        .catch((err) => {
          this.showAlert(err);
        })
        .finally(() => (this.isSpecialtyLoading = false));
    },
    searchProviderString(val) {
      if (val == null || val.length === 0) {
        this.searchProvider = [];
      } // clear for a new search

      if (val == null || val.length < 2) {
        return;
      } // let them type 3 before we search

      if (this.isProviderLoading) {
        return;
      } // Items have already been requested

      this.isProviderLoading = true;

      axios(`${Config.apiRoot}/lookup?type=Provider&value=${val}`)
        .then((res) => {
          this.searchProvider = res.data.message;
        })
        .catch((err) => {
          this.showAlert(err);
        })
        .finally(() => (this.isProviderLoading = false));
    },
    searchPracticeString(val) {
      if (val == null || val.length === 0) {
        this.searchPractice = [];
      } // clear for a new search

      if (val == null || val.length < 2) {
        return;
      } // let them type 3 before we search

      if (this.isPracticeLoading) {
        return;
      } // Items have already been requested

      this.isPracticeLoading = true;

      axios(`${Config.apiRoot}/lookup?type=Practice&value=${val}`)
        .then((res) => {
          this.searchPractice = res.data.message;
        })
        .catch((err) => {
          this.showAlert(err);
        })
        .finally(() => (this.isPracticeLoading = false));
    },
  },
  methods: {
    inRange: function(dist) {
      return (this.searchLocationRange && dist < parseInt(this.searchLocationRange.value)) || this.searchLocation == "";
    },
    showAlert: function(err) {
      this.displayAlert = true;
      this.alertMessage = "There was a problem retrieving data.  Please try again";
    },
    setFocus: function() {
      const tmp = document.getElementsByClassName("v-tab--active");
      if (tmp[0].innerText.indexOf("SPECIALTY") >= 0) {
        this.$refs.objSpecialty.focus();
      } else if (tmp[0].innerText.indexOf("NAME") >= 0) {
        this.$refs.objName.focus();
      } else if (tmp[0].innerText.indexOf("PRACTICE") >= 0) {
        this.$refs.objPractice.focus();
      }
      this.error = true;
    },
    clearFields: function() {
      this.error = false;
      this.selectedItems = [];  // clear results
      this.searchLocation = "";
      this.searchLocationRange = "";
      this.searchGender = { abbr: "No Preference", text: "No Preference" };
      this.searchStatVisits = "";
      this.searchItem = "";
      // this.searchItem = { NPI: null, value: null };
    },
    showProvider: function(selectedProvider) {
      router.push({ name: "provider", params: { selectedProvider: selectedProvider } });
    },
    search: async function() {
      console.log("searching...");
      this.tier1Visible = true;
      this.tier2Visible = false;
      this.tier3Visible = false;

      if (this.searchItem === null || this.searchItem === "") {
        console.log("not searching...");
        this.setFocus();
        return;
      }

      this.$refs.locRange.focus(); // to remove focus from zip
      // if (this.searchLocation != "" && this.searchLocationRange == "") {
      if (
        document
          .getElementsByClassName("v-tab--active")[0]
          .innerText.trim()
          .toUpperCase() === "SPECIALTY" &&
        this.searchLocation != "" &&
        this.searchLocationRange == ""
      ) {
        this.$refs.locRange.focus();
        this.missingRange = true;
        return;
      }
      this.missingRange = false;

      this.displayResults = false;
      this.isSearching = true; // cursor, etc

      // location
      let uCoords = { lat: "", lng: "" };
      if (this.searchLocation) {
        // trying geolocation search

        if (this.searchLocation === "Current Location") {
          const uPos = await getPosition();

          uCoords.lat = uPos.coords.latitude;
          uCoords.lng = uPos.coords.longitude;
        } else {
          uCoords = await this.geocodeZip(this.searchLocation);
        }
      }

      let npi = "";
      let specialty = "";
      let practice = "";
      switch (
        document
          .getElementsByClassName("v-tab--active")[0]
          .innerText.trim()
          .toUpperCase()
      ) {
        case "SPECIALTY":
          specialty = this.searchItem;
          break;
        case "NAME":
          npi = this.searchItem;
          break;
        case "PRACTICE":
          practice = this.searchItem;
          break;
      }

      const range = this.searchLocationRange ? this.searchLocationRange.value : "";
      const qs = `?&npi=${npi}&specialty=${encodeURIComponent(specialty)}&practice=${encodeURIComponent(practice)}&lat=${uCoords.lat}&lng=${uCoords.lng}&range=${range}&gender=${encodeURIComponent(
        this.searchGender.abbr
      )}&stat=${this.searchStatVisits}`;

      axios(`${Config.apiRoot}/search${qs}`)
        .then((res) => {
          if (res.data.message.Status && res.data.message.Status.search("Not Matches found for") > -1) {
            this.selectedItems = [];
          } else {
            this.selectedItems = res.data.message;
            Object.freeze(this.selectedItems);
          }
        })
        .catch((err) => {
          this.showAlert(err);
        })
        .finally(() => {
          this.isPracticeLoading = false;
        });

      this.displayResults = true;
    },
    geocodeZip: function(zip) {
      // returns { lat: xx, lng: xx}
      return new Promise((resolve, reject) => {
        const geocoder = new google.maps.Geocoder();
        geocoder.geocode({ address: zip }, (res, status) => {
          if (status == "OK") {
            resolve({
              lat: res[0].geometry.location.lat(),
              lng: res[0].geometry.location.lng(),
            });
          } else {
            reject({ lat: 0, lng: 0 });
          }
        });
      });
    },
    showLocation(npi) {
      return this.locationNpiToShow === npi;
    },
    toggleLocation: function(npi, count = 1) {
      if (count <= 3) {
        this.locationNpiToShow = npi.NPI;
        this.refreshKey++;
      } else {
        this.showProvider(npi);
      }
    },
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    showMap(addr) {
      window.open(`http://maps.google.com/maps?q=${addr}`, "_blank");
    },
    toPrint() {
      // WADE
      this.$htmlToPaper("printContainer");
    },
    showTier(tier) {
      // if search type is name or practice, show all tiers
      if (
        document.getElementsByClassName("v-tab--active")[0] &&
        document
          .getElementsByClassName("v-tab--active")[0]
          .innerText.trim()
          .toUpperCase() === "SPECIALTY"
      ) {
        if ((tier == "Tier1" && this.tier1Visible) || (tier == "Tier2" && this.tier2Visible) || (tier == "Tier3" && this.tier3Visible)) {
          return true;
        }
      } else {
        return true;
      }
      return false;
    },
    toggleTier(tier) {
      if (tier == 1) {
        this.tier1Visible = !this.tier1Visible;
      } else if (tier == 2) {
        this.tier2Visible = !this.tier2Visible;
      } else if (tier == 3) {
        this.tier3Visible = !this.tier3Visible;
      }
    },
    toggleColor(tier) {
      if ((tier == 1 && this.tier1Visible) || (tier == 2 && this.tier2Visible) || (tier == 3 && this.tier3Visible)) {
        return "primary";
      } else {
        return "light-blue";
      }
    },
  },
};

var getPosition = function(options) {
  return new Promise(function(resolve, reject) {
    navigator.geolocation.getCurrentPosition(resolve, reject, options);
  });
};
</script>

